									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="年龄">
										<el-input v-model="formInline.Age"></el-input>
									</el-form-item>
								</el-col>
							</el-row>
							<el-row :gutter="20">
								<el-col :span="8">
									<el-form-item label="身份证号">
										<el-input v-model="formInline.IDCardNo"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="8">
									<el-form-item label="文化程度">
										<el-input v-model="formInline.Schooling"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="8">
									<el-form-item label="联系方式">
										<el-input v-model="formInline.PhoneNo"></el-input>
									</el-form-item>
								</el-col>
							</el-row>
							<el-row :gutter="20">
								<el-col :span="8">
									<el-form-item label="单位名称">
										<el-input v-model="formInline.UnitName"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="单位性质">
										<el-input v-model="formInline.UnitProperty"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="工种">
										<el-input v-model="formInline.WorkType"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="4">
									<el-form-item label="工龄">
										<el-input v-model="formInline.WorkTime"></el-input>
									</el-form-item>
								</el-col>
							</el-row>
						</div>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-card class="box-card">
				<el-tabs>
					<el-tab-pane label="既往病史">
						<el-row :gutter="20">
							<el-col :span="6">
								<el-form-item label="肝炎">
									<el-date-picker
      v-model="BS_Hepatitis"
      type="date"
	  value-format="yyyy-MM-dd"
      placeholder="选择日期">
    </el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="痢疾">
									<el-date-picker
      v-model="BS_Dysentery"
      type="date"
	  value-format="yyyy-MM-dd"
      placeholder="选择日期">
    </el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="伤寒">
									<el-date-picker
      v-model="BS_Typhia"
      type="date"
	  value-format="yyyy-MM-dd"
      placeholder="选择日期">
    </el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="肺结核">
									<el-date-picker
      v-model="BS_Tuberculosis"
      type="date"
	  value-format="yyyy-MM-dd"
      placeholder="选择日期">
    </el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="皮肤病">
									<el-date-picker
      v-model="BS_Tetter"
      type="date"
	  value-format="yyyy-MM-dd"
      placeholder="选择日期">
    </el-date-picker>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="其他">
									<el-input v-model="formInline.BS_Other"></el-input>
								</el-form-item>
							</el-col>
						</el-row>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-card class="box-card">
				<el-tabs>
					<el-tab-pane label="体征">
						<el-row :gutter="20">
							<el-col :span="6">
								<el-form-item label="心">
									<el-input v-model="formInline.TZ_Heart"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="肝">
									<el-input v-model="formInline.TZ_Liver"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="脾">
									<el-input v-model="formInline.TZ_Spleen"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="肺">
									<el-input v-model="formInline.TZ_Lung"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="皮肤">
									<el-input v-model="formInline.TZ_Skin"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="其他">
									<el-input v-model="formInline.TZ_Other"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.TZ_Doctor"></el-input>
								</el-form-item>
							</el-col>
						</el-row>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-card class="box-card">
				<el-tabs>
					<el-tab-pane label="体征">
						<el-row :gutter="20">
							
							<el-col :span="12">
								<el-form-item label="胸透">
									<el-input type="textarea" :rows="3" v-model="formInline.X_Ray"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.X_Doctor"></el-input>
								</el-form-item>
							</el-col>
						</el-row>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-card class="box-card">
				<el-tabs>
					<el-tab-pane label="检查结果">
						<el-row :gutter="20">
							<el-col :span="6">
								<el-form-item label="痢疾杆菌">
									<el-input v-model="formInline.JC_Dysentery"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.JC_Dysentery_Doctor"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="伤寒或副伤寒">
									<el-input v-model="formInline.JC_Typhia"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.JC_Typhia_Doctor"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="谷丙转氨酶">
									<el-input v-model="formInline.JC_ALT"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.JC_ALT_Doctor"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="HBsAg">
									<el-input v-model="formInline.JC_HBsAg"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.JC_HBsAg_Doctor"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="HBeAg">
									<el-input v-model="formInline.JC_HBeAg"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="6">
								<el-form-item label="医师">
									<el-input v-model="formInline.JC_HBeAg_Doctor"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="其他">
									<el-input v-model="formInline.JC_Other"></el-input>
								</el-form-item>
							</el-col>
						</el-row>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-card class="box-card">
				<el-tabs>
					<el-tab-pane label="结论">
						<el-row :gutter="20">
							
							<el-col :span="12">
								<el-form-item label="检查结论">
									<el-input type="textarea" :rows="3" v-model="formInline.JC_Result"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="卫生监督机构意见">
									<el-input type="textarea" :rows="3" v-model="formInline.Health_Idea"></el-input>
								</el-form-item>
							</el-col>
						</el-row>
					</el-tab-pane>
				</el-tabs>
			</el-card>
			<el-form-item>
			  <el-button class="bottomBtn" type="primary" @click="bottomBtn">保存</el-button>
			  <el-button class="CheckEnd" type="success" @click="CheckEnd">体检完成</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
const axios = require('axios')
export default {
  data () {
    return {
	  formInline: {},
	  BS_Hepatitis:'',
	  BS_Typhia:'',
	  BS_Tuberculosis:'',
	  BS_Tetter:'',
	  BS_Dysentery:'',
	loading: false,
	hospitalCookie:'',
      imgUrl: 'data:image/jpeg;base64,'
	}
  },
  methods: {
    delImg () {
      this.imgUrl = ''
    },
	bottomBtn(){
		this.loading = true
		axios({
        method: 'post',
        url: 'https://www.c65z45.cn/api/Reserve/UpdateHealth',
        data: {
		  Token: this.hospitalCookie,
		  ReserveNo: this.formInline.ReserveNo,
		  BS_Hepatitis: this.BS_Hepatitis,
		  BS_Dysentery: this.BS_Dysentery,
		  BS_Typhia: this.BS_Typhia,
		  BS_Tuberculosis: this.BS_Tuberculosis,
		  BS_Tetter: this.BS_Tetter,
		  BS_Other: this.formInline.BS_Other,
		  TZ_Heart: this.formInline.TZ_Heart,
		  TZ_Liver: this.formInline.TZ_Liver,
		  TZ_Spleen: this.formInline.TZ_Spleen,
		  TZ_Lung: this.formInline.TZ_Lung,
		  TZ_Skin: this.formInline.TZ_Skin,
		  TZ_Other: this.formInline.TZ_Other,
		  TZ_Doctor: this.formInline.TZ_Doctor,
		  X_Ray: this.formInline.X_Ray,
		  X_Doctor: this.formInline.X_Doctor,
		  JC_Dysentery: this.formInline.JC_Dysentery,
		  JC_Dysentery_Doctor: this.formInline.JC_Dysentery_Doctor,
		  JC_Typhia: this.formInline.JC_Typhia,
		  JC_Typhia_Doctor: this.formInline.JC_Typhia_Doctor,
		  JC_ALT: this.formInline.JC_ALT,
		  JC_ALT_Doctor: this.formInline.JC_ALT_Doctor,
		  JC_HBsAg: this.formInline.JC_HBsAg,
		  JC_HBsAg_Doctor: this.formInline.JC_HBsAg_Doctor,
		  JC_HBeAg: this.formInline.JC_HBeAg,
		  JC_HBeAg_Doctor: this.formInline.JC_HBeAg_Doctor,
		  JC_Other: this.formInline.JC_Other,
		  JC_Result: this.formInline.JC_Result,
		  JC_Doctor:this.formInline.JC_Doctor,
		  Health_Idea: this.formInline.Health_Idea
        }
      }).then(res => {
		this.loading = false
        if (res.data.IsSuccess) {
			this.$router.go(-1)
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
        
      }).catch(error => {
        this.$message({
          showClose: true,
          message: '服务器错误',
          type: "error"
        });
        this.loading = false
      })
	},
	CheckEnd(){
		this.confirm()
	},
	confirm() {
				this.$confirm('确认提交体检结果?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
				}).then(() => {
					this.loading = true
					axios({
        method: 'post',
        url: 'https://www.c65z45.cn/api/Reserve/CheckEnd',
        data: {
          Token: this.hospitalCookie,
		  ReserveNo: this.formInline.ReserveNo
        }
      }).then(res => {
		  this.loading = false
        if (res.data.IsSuccess) {
          this.$message({
						type: 'success',
						message: '提交成功!'
					});
			this.$router.go(-1)

        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
        this.loading = false
      }).catch(error => {
		  this.loading = false
        this.$message({
          showClose: true,
          message: '服务器错误',
          type: "error"
        });
        this.loading = false
      })
					
				}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消'
				});
				});
			}
  },
  created(){
	  let inputValues = sessionStorage.getItem("inputValue")
      this.formInline =JSON.parse(inputValues)
      this.hospitalCookie = sessionStorage.getItem("hospitalCookie")
	  this.BS_Hepatitis = this.formInline.BS_Hepatitis
	  this.BS_Dysentery = this.formInline.BS_Dysentery
	  this.BS_Typhia = this.formInline.BS_Typhia
	  this.BS_Tuberculosis = this.formInline.BS_Tuberculosis
	  this.BS_Tetter = this.formInline.BS_Tetter
  }
}
</script>
<style scoped>
	.boxOne {
		display: flex;
	}

	.boxOne>.left {
		width: 30%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.boxOne>.left img {
		display: block;
		width: 2.5cm;
		height: 3.5cm;
		margin-bottom: 10px;
	}

	.boxOne>.right {
		width: 70%;
	}
	.bottomBtn{
		margin: 20px;
		float: right;
	}
	.CheckEnd{
		margin: 20px;
		float: right;
	}
</style>
